import React, { useEffect, useState } from 'react';

function EnglishStudyPage() {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    //    
    setTimeout(() => {
      setIsVisible(true);
    }, 500);
  }, []);

  const redirectToStudyPage = () => {
    // StudyPage   
    window.location.href = '/StudyPage';
  };

  const styles = {
    container: {
      backgroundImage: "url('/img/newworkcity.png')",
      backgroundSize: 'cover',
      height: '110vh',
      overflow: 'hidden',
      backgroundColor: 'rgb(246, 241, 235)',
      padding: '10px',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'flex-start',
    },
    header: {
      textAlign: 'center',
      margin: '100px 60px',
      opacity: isVisible ? 1 : 0,
      transition: 'opacity 1s ease',
    },
    title: {
      fontSize: '64px',
      color: 'yellow',
      margin: '0',
      animation: isVisible ? 'fadeInTitle 1s ease' : '',
    },
    subtitle: {
      fontSize: '30px',
      color: '#fff',
      margin: '0 0 0 -285px',
      animation: isVisible ? 'fadeInSubtitle 1s ease' : '',
      animationDelay: isVisible ? '0.5s' : '1.5s',
      position: 'relative'
    },
    buttonContainer: {
      textAlign: 'center',
      margin: '50px 30px'
    },
    button: {
      width: '200px',
      height: '70px',
      border: '4px solid white',
      borderRadius: '50px',
      backgroundColor: 'transparent',
      color: 'white',
      fontSize: '20px',
      cursor: 'pointer',
      transition: 'color 0.3s, background-color 0.3s', //      
    },
  };

  const buttonHoverStyles = {
    color: '#111', //   
    backgroundColor: '#fff', //   
  };

  return (
    <div style={styles.container}>
      <style>
        {`
          @keyframes fadeInTitle {
            from {
              opacity: 0;
            }
            to {
              opacity: 1;
            }
            animation-fill-mode: none;
          }

          @keyframes fadeInSubtitle {
            from {
              opacity: 0;
            }
            to {
              opacity: 1;
            }
            animation-fill-mode: none;
          }
          .box {
            padding: 25px;
            height: 300px;
            backdrop-filter: blur(3px);
          }
        `}
      </style>
      <div style={styles.header}>
        <div className="fadeInWrapper">
          <h1 style={styles.title}>
            AI    
          </h1>
        </div>
        <div className="fadeInWrapper">
          <p style={styles.subtitle}>
                 
          </p>
        </div>
      </div>
      <div style={styles.buttonContainer}>
        <button 
          className='study_btn' 
          style={styles.button}
          onClick={redirectToStudyPage}
          onMouseOver={(e) => {
            e.target.style.color = buttonHoverStyles.color;
            e.target.style.backgroundColor = buttonHoverStyles.backgroundColor;
          }}
          onMouseOut={(e) => {
            e.target.style.color = styles.button.color;
            e.target.style.backgroundColor = styles.button.backgroundColor;
          }}
        >
          Go learn
        </button>
      </div>
      <div className='box_all' style={{
          position: 'absolute',
          top: '350px',
          left: '35px',
          display: 'flex',
          marginTop: '180px',
          width: '90%'}}>
        <div className='box' style={{
          width: '70%',
          border: 'solid 3px #fff',
          borderRadius: '30px',

        }}>
          <h2 style={{color: '#fff', paddingLeft: '15px'}}>
            YouTube   
          </h2>
          <p style={{color: '#fff', fontSize: '18px', paddingLeft: '15px', lineHeight: '25px'}}>
            YouTube   <br/>
            . <br/>
            ,<br/>
              , <br/>
              <br/>
            . <br/>
              <br/>
             <br/>
                .
          </p>
        </div>
        <div className='box' style={{
          width: '70%',
          border: 'solid 3px #fff',
          borderRadius: '30px',
          margin: '0px 70px'
        }}>
          <h2 style={{color: '#fff', paddingLeft: '15px'}}>
          SERVICE
          </h2>
          <p style={{color: '#fff', fontSize: '18px', paddingLeft: '15px', lineHeight: '25px'}}>
          Butterfly  <br/>
           <br/>
             <br/>
          AI   <br/>
             ,<br/>
            <br/>
              .
          </p>
        </div>
        <div className='box' style={{
          width: '70%',
          border: 'solid 3px #fff',
          borderRadius: '30px'
        }}>
          <h2 style={{color: '#fff', paddingLeft: '15px'}}>
           
          </h2>
          <p style={{color: '#fff', fontSize: '18px', paddingLeft: '15px', lineHeight: '25px'}}>
            <br/>
         AI    <br/>
          ,   
          </p>
        </div>
      </div>
    </div>
  );
}

export default EnglishStudyPage;