import React, { useState } from 'react';
import apiRequest from '../../utils/axios';
import api from '../../constants/api';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { setLoggedIn } from '../../reducers/authReducer';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleLogin = async (e) => {
    e.preventDefault();

    //     
    if (!email.trim()) {
      alert('E-mail .');
      return;
    }

    if (!password.trim()) {
      alert('Password .');
      return;
    }

    const [url, method] = api('login');

    try {
      const response = await apiRequest({
        url,
        method,
        data: {
          email,
          password,
        },
      });
      dispatch(setLoggedIn(true));
      // dispatch(setUserInfo(response.data.user));
      navigate('/StudyPage');
      //     (:    )
    } catch (error) {
      console.error(' ', error);
      //   
    }
  };

  return (
    
    <div style={{
      backgroundImage: `url('/img/LoginBackground.png')`, /*    */
      backgroundSize: 'cover', /*       */
      backgroundPosition: 'center', /*     */
      width: '100%',
      height: '100vh', /*    */
      display: 'flex',
      justifyContent: 'center', /*    */
      alignItems: 'center', /*    */
    }}>
      <div style={{
        width: '900px',
        height: '580px',
        backgroundColor: '#ffffff21;', /*     */
        backdropFilter: 'blur(5px)', /*   */
        padding: '20px',
        borderRadius: '20px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        border: '3px solid #fff'
      }}>
        {/* Sign in Sign up */}
        <div style={{ marginBottom: '20px', color: '#ffffff', fontSize: '24px', display: 'flex', position: 'absolute', top: '-40px', right: '10px' }}>
          <div 
            style={{ color: '#ffffff', fontSize: '24px', cursor: 'pointer', transition: 'color 0.3s', }}
            onClick={() => navigate('/signup')} /* Sign up      */
            onMouseEnter={(e) => e.target.style.color = '#FF7F50'} /*      */
            onMouseLeave={(e) => e.target.style.color = '#ffffff'} /*       */
          >
            Sign up
          </div>
        </div>

        <form onSubmit={handleLogin} style={{textAlign: 'center', margin: '100px'}}>
          <input
            type="email"
            placeholder="E-mail"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            style={{
              width: '750px',
              height: '70px',
              marginBottom: '20px',
              padding: '0 25px',
              borderRadius: '35px',
              border: 'solid 3px #fff',
              fontSize: '1.2rem',
              background: 'none',
              color: '#fff'
            }}
          />
          <input
            type="password"
            placeholder="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            style={{
              width: '750px',
              height: '70px',
              margin: '15px 0px 15px',
              padding: '0 25px',
              borderRadius: '35px',
              border: 'solid 3px #fff',
              fontSize: '1.2rem',
              background: 'none',
              color: '#fff'
            }}
          />
          <button
            type="submit"
            style={{
              width: '805px',
              height: '70px',
              borderRadius: '35px',
              border: 'none',
              padding: '0 25px',
              backgroundColor: '#fff',
              color: '#000',
              fontSize: '1.4rem',
              cursor: 'pointer',
              transition: 'background-color 0.3s',
              margin: '90px'
            }}
          >
            login
          </button>
        </form>
      </div>
    </div>
  );
}

export default Login;
